            <p v-for="(item,index) in productDetail" :key="index">
                <img :src="item.imgSrc" alt="">
            </p>
        </div>
        <!--  -->
        <div class="product-params" id="params">
            <p>
                <strong> : </strong>
                <strong> : </strong>
                <strong> : </strong>
                <strong> : </strong>
